import React from 'react';
import './Header.css';
import logo from '../../assets/logo.png';
import {Link} from "react-router-dom"
import {Button} from "@mui/material"
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
const Header = () =>{
    return (
        <header className="shadow">
            <div class="oferta">
                <b>¡Empezó Cyber Wow!</b> Del 27 julio al 15 de agosto encuentra las mejores ofertas
            </div>
            <div class="nav">
                <figure>
                    <Link to="/"><img className="logo" src={logo}/></Link>
                </figure>
                <ul className="nav-container">
                    <li className="nav-item"><Link to="/"><Button>Principal</Button></Link></li>
                    <li className="nav-item"><Link to="detalle"><Button >Detalle</Button></Link></li>
                    <li className="nav-item">
                        <Link to="/login">
                            <Button >Iniciar Sesion</Button>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <a class="nav-link" data-bs-toggle="offcanvas" aria-controls="offcanvasRight" href="#offcanvasRight">
                            <ShoppingCartIcon/>
                        </a>
                    </li>
                </ul>
            </div>
        </header>
    )
}
export default(Header);