import {Button} from "@mui/material"
const Carrito = () => {
    return (
        <div className="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
            <div className="offcanvas-header">
                <h5 id="offcanvasRightLabel">Carrito de compras</h5>
                <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            <div className="offcanvas-body">
                <ul className="list-group list-group-lg list-group-flush">
                    
                    <li className="list-group-item">
                        <div className="row">
                            <div className="col-4">
                            
                                <a href="producto.html">
                                    <img className="img-fluid" src="https://via.placeholder.com/285x180/?text=Producto" alt="producto" />
                                </a>
                            </div>
                            <div className="col-8">
                            
                                <p className="font-size-sm font-weight-bold mb-6">
                                    <a className="text-body" href="producto.html"> PRODUCTO </a> <br />
                                    <span className="text-muted">S/ 50 x 10</span>
                                </p>

                                <div className="d-flex justify-content-between">
                                    
                                    <div>S/ 1200</div>
                                    
                                    <a className="font-size-xs text-gray-400 ml-auto" href="/carrito/eliminar/12">
                                    Eliminar <i className="fas fa-times"></i> 
                                    </a>
                                </div>
                            </div>
                        </div>
                    </li>
                    
                </ul>
                <div className="line-height-fixed font-size-sm bg-light p-4 d-flex justify-content-between">
                    <strong>Subtotal</strong> <strong className="ml-auto">S/ 1500</strong>
                </div>
                <div className="d-grid gap-2">
                    <Button variant="contained" >Pagar</Button>
                    <Button variant="outlined" >Ver carrito</Button>
                </div>
            </div>
        </div>
    )
}
export default Carrito