import {Grid,Container,Card,FormControl,InputLabel,Input,FormHelperText} from "@mui/material"
const Login = () => {
    return (
        <Container>
            <Grid container spacing={10}>
                <Grid item mt={10} md={6} xs={12}>
                    <Card mt={5} mb={5}>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Correo Electronico</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">We'll never share your email.</FormHelperText>
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Contrase&ntilde;a</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">We'll never share your email.</FormHelperText>
                        </FormControl>
                    </Card>
                </Grid>
                <Grid item mt={10} md={6} xs={12}>
                    <Card mt={5} mb={5}>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Apellido Paterno</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">Primer apellido.</FormHelperText>
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Apellido Materno</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">Segundo apellido.</FormHelperText>
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Nombres</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">Nombres completos.</FormHelperText>
                        </FormControl>
                        <FormControl>
                            <InputLabel htmlFor="my-input">Direccion</InputLabel>
                            <Input id="my-input" aria-describedby="my-helper-text" />
                            <FormHelperText id="my-helper-text">Ubicacion de la vivienda.</FormHelperText>
                        </FormControl>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    )
}
export default Login